import React, { useState } from 'react'
import { Button, Modal, Form, Input } from 'antd'
import './index.css'
import { useAppDispatch, useAppSelector } from '../../../Hooks/storeHook'
import { handleStatus } from '../../../store/ModelStatusSlice'
interface propsType {
    open: boolean,
    onCancel: any
}
const LoginModel: React.FC<propsType> = (props) => {
    const {status}=useAppSelector(state=>({
        status:state.modelStatus.status
    }))
    const dispatch=useAppDispatch()
    const onFinish = (values: any) => {
        console.log('Success:', values);
    };

    const onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal {...props} footer={[]} >
            <div className="login-container" style={{ borderRadius: "4px" }}>
                <div className="login-wrapper">
                    <Form
                        name="basic"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                        onFinishFailed={onFinishFailed}
                        autoComplete="off"
                    >
                        <Form.Item
                            className='loginItem'
                            label=""
                            name="email"
                            rules={[{ required: true, message: '' }]}
                        >
                            <Input placeholder=""/>
                        </Form.Item>

                        <Form.Item
                            className='loginItem'
                            label=""
                            name="password"
                            rules={[{ required: true, message: '' }]}
                        >
                            <Input.Password placeholder="" />
                        </Form.Item>
                        <Form.Item className='loginItem'>
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                        </Form.Item>
                    </Form>
                    {/* <!--  --> */}
                    <div className="mt-10 login-tip">
                        <span style={{cursor:'pointer'}} onClick={()=>dispatch(handleStatus({status:3}))}></span>
                        <span  style={{cursor:'pointer'}} onClick={()=>dispatch(handleStatus({status:4}))} className="float-right">?</span>
                    </div>
                    {/* <!-- -- > */}
                    <div >
                        <div className="social-login-title"></div>
                        <div className="social-login-wrapper">
                            {/* <!--  --> */}
                            <a
                                className="mr-3 iconfont iconweibo"
                                style={{ color: "#e05244" }}

                            />
                            {/* <!-- qq --> */}
                            <a
                                className="iconfont iconqq"
                                style={{ color: "#00AAEE" }}

                            />
                        </div>
                    </div>
                </div >
            </div >
        </Modal >
    )
}
export default LoginModel
